.message{
    width: 100%;
    padding: 1em;
    border:1px solid black;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 2em;
    border-radius: 5px;
}

.success{
    color:green;
    background-color: aliceblue;
    border-color: rgb(0, 124, 7);
}

.error{
    color: red;
    background-color: #f8d7da;
    border-color:#f5c6cb;
}
